import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {Box, Button, Heading} from '@chakra-ui/react';
import InputText from "../../../components/InputText";
import * as thunks from "../../../redux/thunks";


export default function CreateReviewForm() {
    const username = useSelector(state => state.authorization.currentUser.username);
    const [carModel, setCarModel] = useState('');
    const [modification, setModification] = useState('43  ..');
    const [manufactureYear, setManufactureYear] = useState('2021-02-17');
    const [reviewText, setReviewText] = useState('');
    const [pros, setPros] = useState(' ');
    const [cons, setCons] = useState(' ');
    const [advice, setAdvice] = useState(' , ');
    const [ownershipDate, setOwnershipDate] = useState('2022-02-20');
    const [mileage, setMileage] = useState(1488);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(thunks.getUserInfo());
    }, [dispatch]);

    const submitHandler = e => {
        e.preventDefault();
        dispatch(thunks.addReview({authorName: username,
            carModel,
            modification,
            manufactureYear,
            reviewText,
            pros,
            cons,
            advice,
            ownershipDate,
            mileage,}));
    };

    return (<Box p={2} px={5} borderWidth={1} borderRadius={14} boxShadow="lg" w="100%">
        <Box textAlign="center">
            <Heading size="lg"> </Heading>
        </Box>
        <Box my={4} textAlign="left">
            <form>
                <InputText value={carModel} setValue={setCarModel} label={" "}/>
                <InputText value={modification} setValue={setModification} label={""}/>
                <InputText value={manufactureYear} setValue={setManufactureYear} label={" "}/>
                <InputText value={reviewText} setValue={setReviewText} label={" "}/>
                <InputText value={pros} setValue={setPros} label={""}/>
                <InputText value={cons} setValue={setCons} label={""}/>
                <InputText value={advice} setValue={setAdvice} label={""}/>
                <InputText value={ownershipDate} setValue={setOwnershipDate} label={" "}/>
                <InputText value={mileage} setValue={setMileage} label={""}/>
                <Button width="full" mt={4} type="submit" onClick={submitHandler}>  </Button>
            </form>
        </Box>
    </Box>);
}