import React, {useMemo} from "react";
import TableWithPagination from "../../../../components/TableWithPagination";

export default function UserReviewsTable({reviewList}) {
    const reviewData = useMemo(() =>
        reviewList.map(r => ({
            id: r.id,
            modification: r.modification,
            carModel: r.carModel,
            reviewText: r.reviewText,
            mileage: r.mileage,
            advice: r.advice,
            approved: r.approved ? '' : '',
        })), [reviewList]);

    const columns = useMemo(() => [
        {
            Header: '', columns: [
                {Header: '', accessor: 'id'},
                {Header: '', accessor: 'modification'},
                {Header: '', accessor: 'carModel'},
                {Header: '', accessor: 'reviewText'},
                {Header: '', accessor: 'mileage'},
                {Header: '', accessor: 'advice'},
                {Header: '', accessor: 'approved'},
            ]
        },
    ], []);

    return (
        <TableWithPagination columns={columns} data={reviewData}/>
    );
}